import axios from "axios";
import { message } from "antd";
const request = axios.create({
  timeout: 5000,
});

// 
request.interceptors.request.use(
  function (config) {
    // 
    return config;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

// 
request.interceptors.response.use(
  function (response) {
    // 2xx 
    // 
    // let status = response.data.status;
    // if (status === 200) {
    //   message.success(response.data.message);
    // } else {
    //   message.error(response.data.message);
    // }
    return response;
  },
  function (error) {
    //  2xx 
    // 
    message.error("");
    return Promise.reject(error);
  }
);

type API = {
  qq: Function;
  phone: Function;
  LolQQ: Function;
  LolName: Function;
  wbid: Function;
  wbphone: Function;
};

const api: API = {
  qq: (data: string) => request.get("https://zy.xywlapi.cc/qqapi?qq=" + data),
  phone: (data: string) =>
    request.get("https://zy.xywlapi.cc/qqphone?phone=" + data),
  LolQQ: (data: string) =>
    request.get("https://zy.xywlapi.cc/qqlol?qq=" + data),
  LolName: (data: string) =>
    request.get("https://zy.xywlapi.cc/lolname?name=" + data),
  wbid: (data: string) =>
    request.get("https://zy.xywlapi.cc/wbphone?phone=" + data),
  wbphone: (data: string) =>
    request.get("https://zy.xywlapi.cc/wbapi?id=" + data),
};

export default api;
